import { Avatar, IconButton } from '@material-ui/core'
import { AttachFile, MoreVert, SearchOutlined } from '@material-ui/icons'
import React from 'react'
import {ChatHeader} from './ChatHeaderStyled'

function Chat_Header() {
    return (
        <ChatHeader>
            <div className="chat__headerAvatar">
                <Avatar src="data:image/jpeg;base64,"/>
            </div>
            <div className="chat__headerInfo">
                <h3>Room Name</h3>
                <p>Last seen at...</p>
            </div>
            <div className="chat__headerOptions">
                <IconButton>
                    <SearchOutlined />
                </IconButton>
                <IconButton>
                    <AttachFile/>
                </IconButton>
                <IconButton>
                    <MoreVert/>
                </IconButton>
            </div>
            
        </ChatHeader>
    )
}

export default Chat_Header
