import React from 'react'
import {SidebarHeader} from "./SidebarHeaderStyled"
import ChatIcon from '@material-ui/icons/Chat';
import DonutLargeIcon from '@material-ui/icons/DonutLarge'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import {Avatar, IconButton } from '@material-ui/core'

function Sidebar_Header() {
    return (
        <SidebarHeader>
            <div className="sidebar__headerAvatar">
                <Avatar src="data:image/jpeg;base64,"/>
            </div>
            <div className="sidebar__headerOptions">
                <IconButton>
                    <DonutLargeIcon />
                </IconButton>
                <IconButton>
                    <ChatIcon/>
                </IconButton>
                <IconButton>
                    <MoreVertIcon/>
                </IconButton>
            </div> 
        </SidebarHeader>
    )
}

export default Sidebar_Header
